.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(175, 242, 242);
  padding: 10px 20px;
  color: #060505;
  z-index: 11;
}

.navbar-right {
  display: flex;
  position: fixed;
  right: 50px;
  align-items: center;
}

.navbar-left {
  display: flex;
  align-items: center;
}

.navbar h1:hover {
  
  text-shadow: 4px 4px 4px rgba(255, 255, 255, 0.438);
  transition: color 0.9s;
}

.logo {
  width: 50px;
  color: #fff;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin-right: 20px;
  text-decoration: none;
  color:azure;
  font-weight: bold;
  transition: color 0.9s;
}

.nav-links li:hover {
  color: #f39c12;
}

.dropdown {
  position: relative;
}

.dropdownContent {
  display: none;
  position: absolute;
  top: 100%;
  left: -50%;
  background-color: #0000009c;
  list-style: none;
  margin: 0;
  padding: 0;
  border-top: 1px solid #fff;
}

.dropdown.open .dropdownContent {
  display: block;
}

.dropdownContentItem {
  position: relative;
  padding: 10px;
  cursor: pointer;
}

.dropdown:hover .dropdownContent {
  display: block;
  
}

.logoutButton {
  background-color: #5867dadf;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.logoutButton:hover {
  background-color: #25ed9d;
}

.image-toggle-container {
  position: relative;
  width: 40px;
  height: 40px;
  overflow: hidden;
}



@keyframes rotateImage {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(180deg);
  }
}
.image-toggle-container:hover{
  animation: rotateImage 0.5s linear forwards;
}

.image-toggle {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 1s ease;
}

.image-toggle:hover {
  transform: scale(1.1);
}

.circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #3498db;
}

.initial {
  font-size: 36px;
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
}



@media (max-width: 500px) {
  .dropdownContent{
  left: -100%;

  }

  .navbar-right {
    right: 5px;
  }
  h1{
    font-size: 5vw;
  }
  .initial{
    font-size: 10vw;
  }
  .logo{
    width: 10vw;
  }
   .circle{
    width: 12vw;
    height: 12vw;
  }
  .image-toggle-container{
    width: 10vw;
    height: 10vw;
  }
  .navbar{
    display: flex;
    text-align: left;
    align-items: center;
  }
  .nav-links{  
    margin-left: 0;
    padding-left: 5px;
  }
}

